import * as React from "react"
import { withPrefix, Script } from "gatsby"

import Ranking from "./ranking"
import Results from "./results"
import Games from "./games"

const ResultCombo = ({ shv_id }) => {
  function switchToRanking() {
    hideAll();
    document.getElementById("ranking-tab-content").classList.remove("is-hidden");
  }

  function switchToResult() {
    hideAll();
    document.getElementById("results-tab-content").classList.remove("is-hidden");
  }

  function switchToGames() {
    hideAll();
    document.getElementById("games-tab-content").classList.remove("is-hidden");
  }

  function hideAll(){
    document.getElementById('ranking-tab-content').classList.add("is-hidden");
    document.getElementById('results-tab-content').classList.add("is-hidden");
    document.getElementById('games-tab-content').classList.add("is-hidden");
  }

  return (
    <section className="is-primary">
      <div className="container is-fluid my-4">
        <Script src={withPrefix('/result_common.js')} />
        <div className="tabs is-centered">
          <div id="shv_id" data-value={shv_id} className="hidden" />
          <ul>
            <li id="ranking-tab" className="is-active">
              <button id="ranking-button" onClick={switchToRanking} className="button">Tabelle</button>
            </li>
            <li id="results-tab">
              <button id="results-button" onClick={switchToResult} className="button">Resultate</button>
            </li>
            <li id="games-tab">
              <button id="games-button" onClick={switchToGames} className="button">Spiele</button>
            </li>
          </ul>
        </div>

        <div className="container">
          <div id="ranking-tab-content">
            <Ranking />
          </div>
          <div className="is-hidden" id="results-tab-content">
            <Results />
          </div>
          <div className="is-hidden" id="games-tab-content">
            <Games />
          </div>
        </div>
      </div>
    </section>
  )
}

export default ResultCombo
